const showMore = () => {
  console.log('show more');
  $('.filter__content > li:hidden').slice(0, 36).slideDown();
  const elHidden = $('.filter__content > li:hidden').length;
  if( elHidden <= 0) {
    $('.more-products').hide();
  }

};

export default () => {
  const [ htmlList, setHtmlList ] = useState('');

  setTimeout(() => {
    axios.get('/list.html.part')
    .then(response => {
      setHtmlList(response.data);
      setTimeout(() => {
        initProduct();
      }, 500);
    });
  }, 5000);

  return (
    <div className="uk-container uk-section">
      <h3 className="section-title">   :<span>1 245   </span></h3>
      <div className="filter" uk-filter="target: .js-filter">
        <div className="filter__box">
          <div className="uk-grid uk-grid-small uk-child-width-1-4@l uk-child-width-1-4@s uk-child-width-1-2" data-uk-grid>
            <div>
              <h4></h4>
              <ul className="uk-nav">
                <li uk-filter-control="filter: [data-filter-1='']; group: data-filter-1"><a href="#"></a></li>
                <li uk-filter-control="filter: [data-filter-1='']; group: data-filter-1"><a href="#"></a></li>
                <li uk-filter-control="filter: [data-filter-1='']; group: data-filter-1"><a href="#"></a></li>
                <li uk-filter-control="filter: [data-filter-1='-']; group: data-filter-1"><a href="#">-</a></li>
                <li uk-filter-control="filter: [data-filter-1='-']; group: data-filter-1"><a href="#">-</a></li>
                <li uk-filter-control="filter: [data-filter-1='']; group: data-filter-1"><a href="#"></a></li>
              </ul>
            </div>
            <div>
              <h4> </h4>
              <ul className="uk-nav">
                <li uk-filter-control="filter: [data-filter-2='']; group: data-filter-2"><a href="#"></a></li>
                <li uk-filter-control="filter: [data-filter-2='']; group: data-filter-2"><a href="#"></a></li>
                <li uk-filter-control="filter: [data-filter-2='']; group: data-filter-2"><a href="#"></a></li>
                <li uk-filter-control="filter: [data-filter-2='']; group: data-filter-2"><a href="#"></a></li>
                <li uk-filter-control="filter: [data-filter-2='']; group: data-filter-2"><a href="#"></a></li>
                <li uk-filter-control="filter: [data-filter-2='']; group: data-filter-2"><a href="#"></a></li>
              </ul>
            </div>
            <div>
              <h4></h4>
              <ul className="uk-nav">
                <li uk-filter-control="filter: [data-filter-3='']; group: data-filter-3"><a href="#"></a></li>
                <li uk-filter-control="filter: [data-filter-3='']; group: data-filter-3"><a href="#"></a></li>
                <li uk-filter-control="filter: [data-filter-3='']; group: data-filter-3"><a href="#"></a></li>
                <li uk-filter-control="filter: [data-filter-3='']; group: data-filter-3"><a href="#"></a></li>
                <li uk-filter-control="filter: [data-filter-3='']; group: data-filter-3"><a href="#"></a></li>
                <li uk-filter-control="filter: [data-filter-3='']; group: data-filter-3"><a href="#"></a></li>
              </ul>
            </div>
            <div>
              <h4></h4>
              <ul className="uk-nav">
                <li uk-filter-control="filter: [data-filter-4='']; group: data-filter-4"><a href="#"></a></li>
                <li uk-filter-control="filter: [data-filter-4='']; group: data-filter-4"><a href="#"></a></li>
                <li uk-filter-control="filter: [data-filter-4='']; group: data-filter-4"><a href="#"></a></li>
              </ul>
            </div>
            <div className="filter__clear">
              <ul className="uk-nav">
                <li uk-filter-control><a href="#"><span data-uk-icon="icon: close" />  </a></li>
              </ul>
            </div>
          </div>
        </div>
        <div dangerouslySetInnerHTML={{__html: htmlList}} />
        <div className="more-products uk-heading-line uk-text-center" onClick={showMore}><span>  <i data-uk-icon="icon: grid" /></span></div>
      </div>
    </div>
  )
};
